import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import {
  TRAIN_TYPE,
  FACULTY,
  // DORMITORY_ROOM,
  // DORMITORY_BUILDING
} from 'routes/data/category';
import { InfoCard } from 'components/common';
import {
  getQuickLinkTrainTypeNo,
  getQuickLinkFacultyNo,
  getQuickLinkCourseNo,
  getQuickLinkStudyFieldNo,
  getQuickLinkStudyFieldChildNo,
} from 'services/quicklink';
import {
  getStudyFieldGroupNo,
  getKnowledgeNo,
  getSubjectGroupNo,
} from 'services/educationQuicklink';
import routeConstant from 'routes/route/routeConstant';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    ...TRAIN_TYPE,
    service: getQuickLinkTrainTypeNo,
    url: routeConstant.DANH_MUC__DAO_TAO__HE,
  },
  {
    ...FACULTY,
    service: getQuickLinkFacultyNo,
    url: routeConstant.DANH_MUC__DAO_TAO__KHOA_DAO_TAO,
  },
  {
    title: 'Khi ngnh',
    service: getStudyFieldGroupNo,
    url: routeConstant.DANH_MUC__DAO_TAO__KHOI_NGANH,
  },
  {
    title: 'Ngnh o to',
    service: getQuickLinkStudyFieldNo,
    url: routeConstant.DANH_MUC__DAO_TAO__NGANH,
  },
  {
    title: 'CN o to',
    service: getQuickLinkStudyFieldChildNo,
    url: routeConstant.DANH_MUC__DAO_TAO__CHUYEN_NGANH,
  },
  {
    title: 'Kha o to',
    service: getQuickLinkCourseNo,
    url: routeConstant.DANH_MUC__DAO_TAO__KHOA,
  },
  // {
  //   title: 'Xut thn',
  //   service: getQuickLinkComeFromNo,
  //   url: routeConstant.DANH_MUC__TUYEN_SINH__XUAT_THAN,
  //   icon: ComeFromIcon,
  // },
  {
    title: 'Khi kin thc',
    service: getKnowledgeNo,
    url: routeConstant.DANH_MUC__DAO_TAO__KHOI_KIEN_THUC,
  },
  {
    title: 'B mn',
    service: getSubjectGroupNo,
    url: routeConstant.DANH_MUC__DAO_TAO__BO_MON,
  },
];

class DashboardCategory extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardCategory {...props} />
  </Page>
);



